import { Component, OnInit, Input }               from '@angular/core';
import { FormData, StreetSignCount, SignParams, SignType }  from '../data/formData.model';
import { FormDataService }                        from '../data/formData.service';
import { Router }                                 from '@angular/router';
import { Constants }            from '../data/formData.model';

@Component ({
    selector:     'mt-wizard-result',
    templateUrl: './result.component.html'
})

export class ResultComponent implements OnInit {
    title = 'Please check information you entered!';
    @Input() formData: FormData;
    streets: string = '';
    valueD?: number;
    valueDComplex?: number;
    valueC?: number;
    isFormValid: boolean = false;

    constructor(private router: Router, private formDataService: FormDataService) {

    }

    ngOnInit() {
        this.formData = this.formDataService.getFormData();
        this.isFormValid = this.formDataService.isFormValid();
        console.log(this.formData.streetCount);

        if (this.formData.streetCount == StreetSignCount.One) {
            this.streets = "One"
            this.valueD = Constants.formulaD.oneStreet;
            this.valueDComplex = Constants.formulaDMultiple.oneStreet;
        }
        if (this.formData.streetCount == StreetSignCount.Two) {
            this.streets = "Two"
            this.valueD = Constants.formulaD.twoStreet;
            this.valueDComplex = Constants.formulaDMultiple.twoStreet;
        }
        if (this.formData.streetCount == StreetSignCount.Three) {
            this.streets = "Three"
            this.valueD = Constants.formulaD.threeStreet;
            this.valueDComplex = Constants.formulaDMultiple.threeStreet;
        }
        if (this.formData.streetCount == StreetSignCount.Four) {
            this.streets = "Four"
            this.valueD = Constants.formulaD.fourStreet;
            this.valueDComplex = Constants.formulaDMultiple.fourStreet;
        }
        if (this.formData.signType == SignType.Individual) {
          this.valueC = Constants.formulaC.individual;
        }
        if (this.formData.signType == SignType.MultipleYes) {
          this.valueC = Constants.formulaC.multipleComplexYes;
        }
        if (this.formData.signType == SignType.MultipleNo) {
          this.valueC = Constants.formulaC.multipleComplexNo;
        }

        if (this.formData.signType == SignType.MultipleYesComplex) {
          this.formData.area = parseFloat(((Math.pow(Number(this.formData.sqFootage), 1/2.5)+80)*this.valueDComplex).toFixed(2));
          this.formData.brb = this.formData.area
        } else {
          this.formData.area = parseFloat(((Math.pow(Number(this.formData.sqFootage), 1/2.5)+80)*this.valueD).toFixed(2));
          this.formData.brb = parseFloat((this.formData.area * this.valueC).toFixed(2))
        }
        if (!this.formData.brbAvailable) {
          this.formData.brbAvailable = this.formData.brb
        }
        if (this.formData.streetCount == 1) {
          this.formData.brbLimit = parseFloat((50*this.formData.brb/100).toFixed(2))
        }
        if (this.formData.streetCount == 2) {
          this.formData.brbLimit = parseFloat((35*this.formData.brb/100).toFixed(2))
        }
        if (this.formData.streetCount == 3) {
          this.formData.brbLimit = parseFloat((28*this.formData.brb/100).toFixed(2))
        }
        if (this.formData.streetCount == 4) {
          this.formData.brbLimit = parseFloat((25*this.formData.brb/100).toFixed(2))
        }
    }

    goToEditSign(signNumber: number) {
      this.formData.activeFreestandingSign = signNumber;
      this.formData.activeBuildingSign = -1;
      this.router.navigate(['/info1']);
    }

    goToEditBuildingSign(signNumber: number) {
      this.formData.activeFreestandingSign = -1;
      this.formData.activeBuildingSign = signNumber;
      this.router.navigate(['/info1']);
    }

    submit() {
      this.router.navigate(['/preview']);
    }
}



