export namespace Constants {
  export const formulaC = {
    individual: 1.1,
    multipleComplexYes: 0.9,
    multipleComplexNo: 1.0,
  }

  export const formulaD = {
    oneStreet: 1.0,
    twoStreet: 1.41,
    threeStreet: 1.73,
    fourStreet: 2.0,
  }
  export const formulaDMultiple = {
    oneStreet: 0.5,
    twoStreet: 0.7,
    threeStreet: 0.85,
    fourStreet: 1.0,
  }
}

export class FormData {
  area?: number;
  brb?: number;
  brbAvailable?: number;
  brbLimit?: number;
  description: string = '';
  location: string = '';
  firstName: string = '';
  lastName: string = '';
  phoneNumber: string = '';
  signType?: SignType;
  streetCount?: StreetSignCount;
  sqFootage: string = '';
  freestandingSigns: SignParams[] = [];
  activeFreestandingSign?: number;
  buildingSigns: SignParams[] = [];
  activeBuildingSign?: number;

  clear() {
      this.firstName = '';
      this.lastName = '';
      this.description = '';
      this.location = '';
      this.phoneNumber = '';
  }
}

export class SignParams {
  description: string = '';
  completed: boolean;
  brb?: number;
  area?: number;
  height?: number;
  speed?: number;
  setback?: number;
  angle?: number;
  coefficient?: number;
}

export enum SignType {
  Individual, MultipleYes, MultipleNo, MultipleYesComplex
}

export enum StreetSignCount {
  None = 0, One = 1, Two, Three, Four
}

export class Project {
  description: string = '';
  location: string = '';
  firstName: string = '';
  lastName: string = '';
  phoneNumber: string = '';
}



