import { NgModule }           from '@angular/core';
import { BrowserModule }      from '@angular/platform-browser';
import { FormsModule }        from '@angular/forms';

/* App Root */
import { AppComponent }       from './app.component';
import { NavbarComponent }    from './navbar/navbar.component';

/* Wizard Components */
import { ProjectComponent }   from './project/project.component';
import { SignTypeComponent }  from './signtype/signtype.component';
import { ResultComponent }    from './result/result.component';
import { PreviewComponent }    from './preview/preview.component';
import { Info1Component }    from './info1/info1.component';
import { Info2Component }    from './info2/info2.component';

/* Routing Module */
import { AppRoutingModule }   from './app-routing.module';

/* Shared Service */
import { FormDataService }    from './data/formData.service';
import { WorkflowService }    from './workflow/workflow.service';

@NgModule({
    imports:      [ BrowserModule,
                    FormsModule,
                    AppRoutingModule
                  ],
    providers:    [{ provide: FormDataService, useClass: FormDataService },
                   { provide: WorkflowService, useClass: WorkflowService }],
    declarations: [ AppComponent, NavbarComponent, ProjectComponent,
      SignTypeComponent, ResultComponent, PreviewComponent,
      Info1Component, Info2Component ],
    bootstrap:    [ AppComponent ]
})

export class AppModule {}



