import { Component, OnInit }   from '@angular/core';
import { Router }              from '@angular/router';

import { Project }            from '../data/formData.model';
import { Constants }            from '../data/formData.model';
import { FormDataService }     from '../data/formData.service';

@Component ({
    selector:     'mt-wizard-project',
    templateUrl: './project.component.html'
})

export class ProjectComponent implements OnInit {
    title = 'City of South Lake Tahoe Sign Analysis Computer Program';
    project: Project;
    form: any;

    constructor(private router: Router, private formDataService: FormDataService) {
    }

  ngOnInit() {
    this.project = this.formDataService.getProject();
    console.log('Project feature loaded!');
  }

    save(form: any): boolean {
      // console.log(firstname);
      if (!form.valid) {
          return false;
      }

      this.formDataService.setProject(this.project);
      return true;
    }

    goToNext(form: any) {
        if (this.save(form)) {
            // Navigate to the work page
            this.router.navigate(['/signtype']);
        }
    }
}



