import { NgModule }             from '@angular/core';
import { Routes, RouterModule } from '@angular/router';

import { ProjectComponent }    from './project/project.component';
import { SignTypeComponent }    from './signtype/signtype.component';
import { ResultComponent }      from './result/result.component';
import { Info1Component }      from './info1/info1.component';
import { Info2Component }      from './info2/info2.component';
import { PreviewComponent }      from './preview/preview.component';

import { WorkflowGuard }        from './workflow/workflow-guard.service';
import { WorkflowService }      from './workflow/workflow.service';


export const appRoutes: Routes = [
  // Project Description Route
  { path: 'project',  component: ProjectComponent },
  { path: 'signtype',  component: SignTypeComponent },
  { path: 'result',  component: ResultComponent, canActivate: [WorkflowGuard] },
  { path: 'info1',  component: Info1Component },
  { path: 'info2',  component: Info2Component, canActivate: [WorkflowGuard] },
  { path: 'preview',  component: PreviewComponent },
  { path: '',   redirectTo: '/project', pathMatch: 'full' },
  { path: '**', component: ProjectComponent }
];

@NgModule({
  imports: [RouterModule.forRoot(appRoutes, { useHash: true} )],
  exports: [RouterModule],
  providers: [WorkflowGuard]
})

export class AppRoutingModule {}



