import { Injectable }                        from '@angular/core';

import { FormData, Project, SignType, StreetSignCount, SignParams }       from './formData.model';
import { WorkflowService }                   from '../workflow/workflow.service';
import { STEPS }                             from '../workflow/workflow.model';

@Injectable()
export class FormDataService {

  private formData: FormData = new FormData();
  private isProjectFormValid: boolean = false;
  private isSignTypeFormValid: boolean = false;

  constructor(private workflowService: WorkflowService) {
  }

  getFormData(): FormData {
      // Return the entire Form Data
      return this.formData;
  }

  resetFormData(): FormData {
      // Reset the workflow
      this.workflowService.resetSteps();
      // Return the form data after all this.* members had been reset
      this.formData.clear();
      this.isProjectFormValid = this.isSignTypeFormValid = false;
      return this.formData;
  }

  isFormValid() {
      // Return true if all forms had been validated successfully; otherwise, return false
      return this.isProjectFormValid &&
              this.isSignTypeFormValid;
  }

  getProject(): Project {
    // Return the Project data
    var project: Project = {
      firstName: this.formData.firstName,
      lastName: this.formData.lastName,
      description: this.formData.description,
      location: this.formData.location,
      phoneNumber: this.formData.phoneNumber
    };
    return project;
  }

  setProject(data: Project) {
      // Update the Project data only when the Project Form had been validated successfully
      this.isProjectFormValid = true;
      this.formData.firstName = data.firstName;
      this.formData.lastName = data.lastName;
      this.formData.description = data.description;
      this.formData.location = data.location
      this.formData.phoneNumber = data.phoneNumber
      // Validate Project Step in Workflow
      this.workflowService.validateStep(STEPS.project);
  }

  getSignType() : [SignType, StreetSignCount, string] {
      // Return the work type
      return [this.formData.signType, this.formData.streetCount, this.formData.sqFootage];
  }

  getCoefficient(setback: number, angle: number) {
    console.log(setback + " - " + angle);
    if (setback >= 5 && setback <= 50) {
      if (angle >= 0 && angle <= 15) {
        return 0.5
      }
      if (angle >= 16 && angle <= 30) {
        return 0.6
      }
      if (angle >= 31 && angle <= 45) {
        return 0.7
      }
      if (angle >= 46 && angle <= 60) {
        return 0.8
      }
      if (angle >= 61 && angle <= 75) {
        return 0.9
      }
      if (angle >= 76 && angle <= 90) {
        return 1.0
      }
    }

    if (setback > 50 && setback <= 100) {
      if (angle >= 0 && angle <= 15) {
        return 0.55
      }
      if (angle >= 16 && angle <= 30) {
        return 0.65
      }
      if (angle >= 31 && angle <= 45) {
        return 0.75
      }
      if (angle >= 46 && angle <= 60) {
        return 0.85
      }
      if (angle >= 61 && angle <= 75) {
        return 0.95
      }
      if (angle >= 76 && angle <= 90) {
        return 1.05
      }
    }

    if (setback > 100 && setback <= 150) {
      if (angle >= 0 && angle <= 15) {
        return 0.6
      }
      if (angle >= 16 && angle <= 30) {
        return 0.7
      }
      if (angle >= 31 && angle <= 45) {
        return 0.8
      }
      if (angle >= 46 && angle <= 60) {
        return 0.9
      }
      if (angle >= 61 && angle <= 75) {
        return 1.0
      }
      if (angle >= 76 && angle <= 90) {
        return 1.10
      }
    }

    if (setback > 150 && setback <= 200) {
      if (angle >= 0 && angle <= 15) {
        return 0.65
      }
      if (angle >= 16 && angle <= 30) {
        return 0.75
      }
      if (angle >= 31 && angle <= 45) {
        return 0.85
      }
      if (angle >= 46 && angle <= 60) {
        return 0.95
      }
      if (angle >= 61 && angle <= 75) {
        return 1.05
      }
      if (angle >= 76 && angle <= 90) {
        return 1.15
      }
    }

    if (setback > 200 && setback <= 250) {
      if (angle >= 0 && angle <= 15) {
        return 0.7
      }
      if (angle >= 16 && angle <= 30) {
        return 0.8
      }
      if (angle >= 31 && angle <= 45) {
        return 0.9
      }
      if (angle >= 46 && angle <= 60) {
        return 1.0
      }
      if (angle >= 61 && angle <= 75) {
        return 1.1
      }
      if (angle >= 76 && angle <= 90) {
        return 1.2
      }
    }

    if (setback > 250 && setback <= 300) {
      if (angle >= 0 && angle <= 15) {
        return 0.75
      }
      if (angle >= 16 && angle <= 30) {
        return 0.85
      }
      if (angle >= 31 && angle <= 45) {
        return 0.95
      }
      if (angle >= 46 && angle <= 60) {
        return 1.05
      }
      if (angle >= 61 && angle <= 75) {
        return 1.15
      }
      if (angle >= 76 && angle <= 90) {
        return 1.25
      }
    }

    if (setback > 300 && setback <= 350) {
      if (angle >= 0 && angle <= 15) {
        return 0.8
      }
      if (angle >= 16 && angle <= 30) {
        return 0.9
      }
      if (angle >= 31 && angle <= 45) {
        return 1.0
      }
      if (angle >= 46 && angle <= 60) {
        return 1.1
      }
      if (angle >= 61 && angle <= 75) {
        return 1.2
      }
      if (angle >= 76 && angle <= 90) {
        return 1.3
      }
    }

    if (setback > 350 && setback <= 400) {
      if (angle >= 0 && angle <= 15) {
        return 0.85
      }
      if (angle >= 16 && angle <= 30) {
        return 0.95
      }
      if (angle >= 31 && angle <= 45) {
        return 1.05
      }
      if (angle >= 46 && angle <= 60) {
        return 1.15
      }
      if (angle >= 61 && angle <= 75) {
        return 1.25
      }
      if (angle >= 76 && angle <= 90) {
        return 1.35
      }
    }

    if (setback > 400 && setback <= 450) {
      if (angle >= 0 && angle <= 15) {
        return 0.9
      }
      if (angle >= 16 && angle <= 30) {
        return 1.0
      }
      if (angle >= 31 && angle <= 45) {
        return 1.1
      }
      if (angle >= 46 && angle <= 60) {
        return 1.2
      }
      if (angle >= 61 && angle <= 75) {
        return 1.3
      }
      if (angle >= 76 && angle <= 90) {
        return 1.4
      }
    }

    if (setback > 450 && setback <= 500) {
      if (angle >= 0 && angle <= 15) {
        return 0.95
      }
      if (angle >= 16 && angle <= 30) {
        return 1.05
      }
      if (angle >= 31 && angle <= 45) {
        return 1.15
      }
      if (angle >= 46 && angle <= 60) {
        return 1.25
      }
      if (angle >= 61 && angle <= 75) {
        return 1.35
      }
      if (angle >= 76 && angle <= 90) {
        return 1.45
      }
    }

    if (setback > 500) {
      if (angle >= 0 && angle <= 15) {
        return 1.0
      }
      if (angle >= 16 && angle <= 30) {
        return 1.1
      }
      if (angle >= 31 && angle <= 45) {
        return 1.2
      }
      if (angle >= 46 && angle <= 60) {
        return 1.3
      }
      if (angle >= 61 && angle <= 75) {
        return 1.4
      }
      if (angle >= 76 && angle <= 90) {
        return 1.5
      }
    }

  }

  setSignType(signType: SignType, streetCount: StreetSignCount, sqFootage: string, freestandingSigns: StreetSignCount, buildingSigns: StreetSignCount) {
      // Update the work type only when the Work Form had been validated successfully
      this.isSignTypeFormValid = true;
      this.formData.signType = signType;
      this.formData.streetCount = streetCount;
      this.formData.sqFootage = sqFootage;
      this.formData.freestandingSigns = [];
      if (freestandingSigns == 0) {
        this.formData.freestandingSigns = [];
      } else {
        for (var i=0; i<freestandingSigns; i++) {
          var signParams: SignParams = {
            description: String(i+1),
            completed: false
          }
          this.formData.freestandingSigns.push(signParams)
        }
      }
      this.formData.buildingSigns = [];
      if (buildingSigns == 0) {
        this.formData.buildingSigns = [];
      } else {
        for (var i=0; i<buildingSigns; i++) {
          var signParams: SignParams = {
            description: String(i+1),
            completed: false
          }
          this.formData.buildingSigns.push(signParams)
        }
      }

      // Validate Work Step in Workflow
      this.workflowService.validateStep(STEPS.signtype);
  }

  setFreestandingSign(orderNumber: number) {
    this.formData.freestandingSigns[orderNumber].completed = true;
  }

}



