import { Component, OnInit }   from '@angular/core';
import { Router }              from '@angular/router';

import { SignType }            from '../data/formData.model';
import { StreetSignCount }        from '../data/formData.model';
import { FormDataService }     from '../data/formData.service';

@Component ({
    selector:     'mt-wizard-sign-type',
    templateUrl: './signtype.component.html',
    styleUrls: ['./signtype.component.css'],
})

export class SignTypeComponent implements OnInit {
    title = 'Business Parameters';
    signType: SignType;
    sqFootage: string;
    streetCount: StreetSignCount;
    freestandingCount: StreetSignCount;
    buildingCount: StreetSignCount;
    form: any;

    constructor(private router: Router, private formDataService: FormDataService) {
    }

    mouseHover(e) {
      console.log('hovered', e);
    }

    ngOnInit() {
        [this.signType, this.streetCount, this.sqFootage] = this.formDataService.getSignType();
        console.log('Sign Type loaded!');
    }

    save(form: any): boolean {
        if (!form.valid) {
            return false;
        }

        this.formDataService.setSignType(this.signType, this.streetCount, this.sqFootage, this.freestandingCount, this.buildingCount);
        return true;
    }

    goToPrevious(form: any) {
        if (this.save(form)) {
            // Navigate to the project page
            this.router.navigate(['/project']);
        }
    }

    goToNext(form: any) {
        if (this.save(form)) {
            // Navigate to the address page
            this.router.navigate(['/result']);
        }
    }

    isDisabled1() {
      if (this.streetCount < 1 || this.streetCount == null) {
        if (this.freestandingCount > 1) {
          this.freestandingCount = null
        }
        return true;
      }
      return null;
    }

    isDisabled2() {
      if (this.streetCount < 2 || this.streetCount == null) {
        if (this.freestandingCount > 1) {
          this.freestandingCount = null
        }
        return true;
      }
      return null;
    }

    isDisabled3() {
      if (this.streetCount < 3 || this.streetCount == null) {
        if (this.freestandingCount > 2) {
          this.freestandingCount = null
        }
        return true;
      }
      return null;
    }

    isDisabled4() {
      if (this.streetCount < 4 || this.streetCount == null) {
        if (this.freestandingCount > 3) {
          this.freestandingCount = null
        }
        return true;
      }
      return null;
    }
}



